.search {

  &__title {
    margin: 90px 0 30px 0;
    font-family: $font-base-book;
    font-size: 40px;
    font-weight: normal;
    color: #fff;
  }

}

.search-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 750px;
  background: #fff;
  border-radius: 50px;
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);
  outline: none;
  transition: all ease-in-out .2s;

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(235, 63, 87, 1);
  }

  &__select {
    width: 140px;
    padding: 15px 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    background: transparent;
    border: none;
    cursor: pointer;
    outline: none;
  }

  &__input {
    width: 100%;
    flex-grow: 1;
    padding: 17px 0 17px 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    background: transparent;
    border: none;
    line-height: 1;
    outline: none;
  }

  &__btn {
    padding: 17px 25px;
    font-size: 20px;
    color: $color-link;
    background: transparent;
    border-radius: 50px;
    border: none;
    outline: none;
  }

}

.search-sub-link {
  display: inline-block;
  margin-top: 30px;
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 24px;
  color: #fff;
  text-decoration: none;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .5;
  }

  & [class^="icon-"], & [class*=" icon-"] {
    font-size: 20px;
    position: relative;
    top: 3px;
    padding-left: 10px;
  }

}

