/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .search {

    &__title {
      margin: 30px 0;
      font-size: 24px;
      text-align: center;
    }

  }

  .search-input {

    &__select {
      width: 120px;
      padding: 10px 20px;
      font-size: 18px;
    }

    &__input {
      padding: 12px 0 12px 20px;
      font-size: 18px;
    }

    &__btn {
      padding: 12px 25px;
      font-size: 18px;
    }

  }

  .search-sub-link {
    display: block;
    margin-top: 10px;
    font-size: 20px;
    text-align: center;
  }

}

