.card {
  width: 100%;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .9);
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);

  &__header {
    padding: 20px 25px 0 25px;
  }

  &__body {
    padding: 20px 25px 0 25px;
  }

  &__footer {
    padding: 20px 0 0 0;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 30px;
    color: $color-title;
  }

  &__ask-lib-text {
    margin: 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;
  }

}

.card-nav {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #dde2ec;

  &__item {
    flex-grow: 1;
    padding: 17px 0 23px 0;
    text-align: center;
    border-right: 1px solid #dde2ec;

    &:last-child {
      border-right: none;
    }

  }

  &__link {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

}

.card-transparent {
  width: 100%;

  &__header {
    margin: 70px 0 50px;
    display: flex;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 36px;
    color: $color-title;
    line-height: 1;
  }

  &__link {
    display: inline-block;
    margin-top: 11px;
    margin-left: 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;
    white-space: nowrap;

    &:hover {
      text-decoration: underline;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .card {
    background-color: #fff;

    &_md-mt {
      margin-top: 70px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .card {

    &_md-mt {
      margin-top: 50px;
    }

    &__header {
      padding: 20px 25px 0 25px;
    }

    &__body {
      padding: 20px 25px 0 25px;
    }

    &__footer {
      padding: 20px 0 0 0;
    }

    &__title {
      font-size: 24px;
    }

    &__ask-lib-text {
      font-size: 18px;
      line-height: 1.3;
    }

  }

  .card-nav {

    &__item {
      padding: 17px 0 23px 0;
    }

    &__link {
      font-size: 18px;
    }

  }


  .card-transparent {

    &__header {
      margin: 50px 0 25px;
      flex-direction: column;
    }

    &__title {
      font-size: 24px;
    }

    &__link {
      display: inline-block;
      margin-top: 10px;
      margin-left: 0;
      font-size: 18px;
    }

  }

}

